import { registerPlugin } from '@capacitor/core';

import type { ContentsquareCAPPlugin, DynamicVarItem, TransactionItem } from './definitions';

const ContentsquareCAP = registerPlugin<ContentsquareCAPPlugin>('ContentsquareCAP', {
  web: () => import('./web').then(m => new m.ContentsquareCAPWeb())
});

// Use ContentsquarePlugin to simulate a proxy. Here we can add some logic on the Javascript side before calling native methods.
const ContentsquarePlugin = {
  optIn: function () {
    console.log('Opt in proxy');
    ContentsquareCAP.optIn();
  },
  optOut: function () {
    console.log('Opt out proxy');
    ContentsquareCAP.optOut();
  },
  sendTransaction(transactionItem: TransactionItem): Promise<any> {
    console.log('Send transaction proxy', transactionItem);
    return ContentsquareCAP.sendTransaction(transactionItem);
  },
  sendDynamicVar(dynamicVarItem: DynamicVarItem): Promise<any> {
    console.log('Send sendDynamicVar proxy', dynamicVarItem);
    const dynVarValue = dynamicVarItem.dynVarValue;
    if (typeof dynVarValue === 'string') {
      return ContentsquareCAP.sendDynamicVarWithStringValue(dynamicVarItem);
    } else if (Number.isInteger(dynVarValue) && dynVarValue >= 0) {
      return ContentsquareCAP.sendDynamicVarWithIntValue(dynamicVarItem);
    } else {
      let callbackError = TypeError("The value of the DynamicVar should be either a string or an unsigned integer");
      return new Promise((_resolve, reject) => {
        reject(callbackError);
      });
    }
  },
  sendScreenName(screenName: string): Promise<any> {
    console.log('Send sendScreenName proxy', screenName);
    return ContentsquareCAP.sendScreenName({ name: screenName });
  }
};


export * from './definitions';
export { ContentsquareCAP, ContentsquarePlugin };


